	module.exports = {"data":{"site":{"siteMetadata":{"title":"Саентология Тома Круза","author":"Том Круз"}},"markdownRemark":{"id":"/home/gleki/ccc/src/pages/2017-07-05-leah-remini/index.md absPath of file >>> MarkdownRemark","html":"<p>Лия Ремини открывает страшную правду о саентологии.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/23bab4302536028e2e0da8ee5bdacab8-0098e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 300px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Лия\"\n        title=\"\"\n        src=\"/static/23bab4302536028e2e0da8ee5bdacab8-0098e.png\"\n        srcset=\"/static/23bab4302536028e2e0da8ee5bdacab8-3dd37.png 148w,\n/static/23bab4302536028e2e0da8ee5bdacab8-3dede.png 295w,\n/static/23bab4302536028e2e0da8ee5bdacab8-0098e.png 400w\"\n        sizes=\"(max-width: 300px) 100vw, 300px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<hr>\n<p>Бывший саентолог Лия Ремини уже несколько лет ведёт борьбу против политики саентологической псевдоцеркви в шоу «Саентология и её последствия» (Scientology and Aftermath).</p>\n<p>В течение первого и второго сезонов шоу Лия говорила, что не хочет вникать в конкретные «верования» саентологии. Она подчёркивала, что её миссия - разоблачать злоупотребления саентологии.</p>\n<p>Но вот в сентябре 2017 года она неожиданно раскрыла подробности высшего секретного уровня на саентологическом «мосту к полной свободе» - уровня ОТ8.</p>\n<p>Лия раскрыла подробности этого уровня так, как не делал ещё никто другой.</p>\n<p>Лия и её мама Вики обнажили вершину саентологического «Моста».</p>\n<h1>Диалог между Лией и Вики</h1>\n<p>Лия: Мама, когда ты добралась до уровня ОТ8, ты сказала мне тогда, что ты покончила с саентологией.</p>\n<p>Вики: Да.</p>\n<p>Лия: Это из-за того, что OT8 оказался не таким, каким ты думала?</p>\n<p>Вики: Да, я не думала, что это окажется так.</p>\n<p>Лия: А чего ты ожидала?</p>\n<p>Вики: Я ожидала духовной свободы навсегда. Навсегда.</p>\n<p>Лия: Когда я разговаривала с тобой на эту тему, я спрашивала: «Это всё, чего ты хотела, мама? Это всё, на что ты пожертвовала всю свою жизнь?» И ты сказала: «Не сейчас, Лия».</p>\n<p>Вики: Верно.</p>\n<p>Лия: Понятно. Так что в основном то, что мама говорила, было: «Нет, я не могу говорить с тобой об этом сейчас, потому что они слушают или они собираются удерживать меня здесь, если они узнают, что я не счастлива».</p>\n<p>Вики: Совершенно верно.</p>\n<p>Лия: Да. Итак, это цитата от того, кто завершил уровень OT8. [Лия читает комментарий в блоге Майка Риндера] «Я думал, что OT8 был концом Моста. Было логично считать, что ты сам создал свой собственный реактивный ум».</p>\n<p>Вики: Создали его, точно.</p>\n<p>Лия: [Комментарий к прежнему чтению] Озарение в OT8 состоит в том, что твой кейс [память, включая прошлые жизни] состоит из памятей боди-тэтанов [бесплотных духов-паразитов, прикреплённых к телу], и вты никогда не был тем, кого эти боди-тэтаны помнили как твоё прошлое».</p>\n<p>Вики: Верно.</p>\n<p>Лия: [Продолжая читать комментарий] «И на вершине моста, потратив миллион долларов или более, ты снова стал тем, кем был, когда ты впервые вошёл через дверь дианетического центра или организации».</p>\n<p>Вики: Правильно, тем, кем ты и был в самом начале.</p>\n<p>Лия: Тем, кем ты и был в самом начале.</p>\n<p>Вики: Верно. Это то, что я говорила всё это время.</p>\n<p>Лия: Ну, здесь нет ли здесь кого-нибудь… позвольте мне спросить операторов. Знаете ли Вы, сэр, что вы тот, кто вы есть?</p>\n<p>Парень-оператор: Абсолютно.</p>\n<p>Лия: Интересно. И Вы никогда не проходили саентологических процедур.</p>\n<p>Парень-оператор: Совсем нет.</p>\n<p>Майк Риндер: Мы только что сэкономили вам миллионы.</p>\n<p>(немного позже)</p>\n<p>Майк Риндер: И окончательная морковка: Вы узнаете, кто Вы есть на самом деле на уровне OT9.</p>\n<h1>Комментарий</h1>\n<p>Риндер имел в виду уровень OT9, который в настоящее время не существует. Но лидер саентологии Дэвид Мицкевич обещает членам церкви, что скоро он выпустит ОТ9 и ОТ10. (Бывший топ-офицер Марти Разбан в одной из своих книг сообщал, что уровней выше OT8 не существует, хотя Л. Рон Хаббард говорил о том, чтобы в конечном итоге довести их до OT15.) Ещё одна деталь, о которой нужно помнить: OT8 предоставляется только на частном круизном судне саентологов, Freewinds, который курсирует между несколькими островами Карибского моря.</p>\n<p>А какое мнение у других членов «церкви», достигшие OT8 испытывали этот момент в своём движении по мосту?</p>\n<p>Мэри Кан сообщила, что, хотя OT8 не был таким, каким она надеялась его увидеть, это не было её самой большой заботой в то время.</p>\n<p>«OT8 был очень коротким, и я закончила его после одной или двух сессий. И хотя это было не то, чего я ожидала, не это было моей основной болью. И раньше были признаки того, что на мосту очень много того, что не имеет смысла, но тем не менее я продолжала. Поэтому тот факт, что OT8 оказался другим, не был для меня каким-то новым феноменом… Самым страшным в саентологии - и в моём пребывании на корабле, и в OT 8 - было совсем другое дерьмо. Просто невозможно описать, насколько ужасно там было находиться, но к тому времени я уже оказалась в ловушке».</p>","frontmatter":{"title":"Лия Ремини. Страшная правда о саентологии","date":"July 05, 2017"}}},"pathContext":{"path":"/leah-remini/"}}

